<!-- 手机Loading -->
<template>
  <div class="loading-wrapper-phone">
    <div class="toast toast-loading">
      <img class="closeModal" @click="closeIntergalModal" :src="close_icon" alt="关闭按钮">
      <img  class="market" :src="img_src" alt="商城暂未开通">
      <div class="modalText">
        <span>积分商城暂未开通</span><br>
        <span>敬请期待</span>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: [
    //   'title'
    ],
    data() {
      return {
        img_src: require('../assets/img/jf25h7k3.png'),
        close_icon: require('../assets/img/guanbi.png')
      }
    },
    methods: {
        closeIntergalModal(){
        this.$emit('closeIntergalModal',0);
      },
    }
  }
</script>
<style lang="less" scoped>
  .loading-wrapper-phone {
    position: fixed;
    z-index: 1000;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1000000000;
    background: rgba(0, 0, 0, .5);
    .toast {
      left:50%;
      top: 50%;
      transition-duration: .2s;
      z-index: 100;
      position: fixed;
      min-height: 400px;
      border-radius: 5px;
      color: #fff;
      transform: translate(-50%,-50%);
      &>.market{
        max-width: 500px;
      }
      &>.closeModal{
        position: absolute;
        top: 0;
        right: 0;
        width: 15%;
      }
      &>.modalText{
        position: absolute;
        top: 70%;
        left: 50%;
        transform: translate(-50%,-50%);
        text-align: center;
        font-size: 25px;
        &>span{
          color: #000;
        }
      }
    }
  }
</style>


