<style lang="less" scoped>
  body{
    height: auto !important;
  }
  #player {
    width: 100%;
    height: 400px !important;
    position: fixed;
    top: 0;
    background-color: #f3e309;
    z-index: 9;
    div {
      height: 400px !important;
    }
  }
  .swiper-container{
    padding-top: 600px;
    margin-bottom: 0;
  }
  #playerRecord {
    position: fixed;
    top: 0;
    left: 50%;
    width: 100%;
    max-width: 40rem;
    transform: translateX(-50%);
    height: 400px !important;
    background-color: #f3e309;
    z-index: 9;
    background-image: url('../../assets/img/default_blue.png');
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: cover;
    &>div {
      height: 400px !important;
      background-image: url('../../assets/img/nolive_blue.png');
      background-size: 50% 70%;
      background-repeat: no-repeat;
      background-position: center center;
    }
  }
  .middle>div>img {
    vertical-align: middle;
  }
  .middle>div>span {
    vertical-align: middle;
  }
  .middle>div>a {
    vertical-align: middle;
  }
  .chat_detail {
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    background-color: #fff;
    min-height: 600px;
    padding-bottom: 100px;
  }
  
  .emoji {
    background-image: url('../../../static/images/smile.png');
    background-size: contain;
    background-repeat: no-repeat;
    width: 90px;
    height: 90px;
    vertical-align: middle;
    border: none !important;
    outline: none !important;
    background-color: transparent;
  }
  .middle {
    display: -webkit-flex;
    display: flex;
    height: 120px;
    position: fixed;
    top: 400px;
    left: 0;
    z-index: 9;
    background-color: #fff;
    width: 100%;
    background-color: #fff;
    line-height: 120px;
  }
  .middle>div {
    height: 120px;
  }
  .middle>div:nth-child(1) {
    -webkit-box-flex: 5;
    flex: 5;
    padding-left: 30px;
  }
  .middle>div:nth-child(1)>img {
    width: 20%;
  }
  .middle>div:nth-child(1)>span {
    display: inline-block;
  }
  .middle>div:nth-child(2) {
    -webkit-box-flex: 5;
    flex: 5;
    text-align: right;
    padding-right: 30px;
  }
  .middle>div:nth-child(2)>div{
    vertical-align: middle;
    img{
      width: 50px;
      vertical-align: middle;
      display: inline-block;
      margin-right: 10px;
    }
  }
  .tab_interactive{
    padding-top: 530px;
    &>div{
      background-color: #fff;
      font-size: 35px;
      text-align: center;
      padding: 20px 0;
    }
  }
  .objectives{
    height: 50vh;
    background-color: #fff;
    padding: 20px 30px;
    font-size: 30px;
  }
</style>
<template>
  <div>
    <!-- 直播页面 视频直播区域 START -->
    <div id='player' class="live_pannel" v-if="player"></div>
    <remote-js src="http://live.polyv.net/assets/js/jquery.min.js"></remote-js>
    <remote-js src="//player.polyv.net/script/polyvplayer.min.js"></remote-js>
    <!-- 直播页面 视频直播区域 END -->
    <!-- 中部的签到区域 START -->
    <section class="middle">
      <div>
        <img :src="vodLiveData && vodLiveData.teacherAvatar ? vodLiveData.teacherAvatar : defaultAvatar" alt="讲师头像">
        <span>{{vodLiveData && vodLiveData.teacherName}}</span>
      </div>
      <div>
        <!-- <div @click="intergal">
        <img src="../../../static/images/qiandao.png" alt="签到">打开签到
        </div> -->
      </div>
    </section>
    <!-- 签到区域 END -->
    <!-- 直播互动区域 START -->
    <section class="tab_interactive">
      <div>课程简介</div>
    </section>
    <section class="objectives">
      {{objectives.data.objectives ? objectives.data.objectives : '暂无简介'}}
    </section>

     <!-- 签到积分 START -->
    <transition-group enter-active-class="zoomIn" leave-active-class="zoomOut">
      <intergal-modal key="intergalModal"  class="animated" v-if="showIntergal" v-on:closeIntergalModal="closeIntergalModal"></intergal-modal>
    </transition-group>
    <!-- 签到积分 END -->
  </div>
</template>
<script>
  import intergalModal from '@/components/getIntergal'
  import { mapState } from 'vuex'
  export default {
    name: 'live',
    components: {
      // 创建script标签
      'remote-js': {
        render(createElement) {
          return createElement('script', {
            attrs: {
              type: 'text/javascript',
              src: this.src
            }
          });
        },
        props: {
          src: {
            type: String,
            required: true
          },
        },
      },
      intergalModal,
    },
    data() {
      return {
        // 在线学生数量
        studentCount: 100,
        // 讲师昵称
        teacherName: '',
        rfliveStatus: 1,
        // 录播视频路径设置
        recordFilesSrc: '',
        // 直播控件设置状态
        player: 0,
        // 显示积分
        showIntergal: 0,
        // 标题
        title: '报名课程',
        vodLiveData: null,
        objectives: {
          data: {
            objectives: null,
          }
        },
        defaultAvatar: require('../../assets/img/eb5974c81833c9fc043a700cfe21aa9d.jpg')

      }
    },
    mounted() {


      window.onscroll = () => {
        return
      }

      this.player = 1
      // 延迟初始化直播播放器
      setTimeout(() => {
        polyvObject('#player').videoPlayer({
          'width': '100%',
          'height': '200px',
          'vid': this.$route.params.vid
        });
      }, 1000)

      this.axios('/api/live', {
        params: {
          courseId: this.$route.params.id
        }
      }).then(data => {
        if(data.data && data.data.result){
          data.data.result.forEach(element => {
            if(element && element.courseId && element.liveContentJson.videoContent && element.liveContentJson.videoContent.vid && !this.vodLiveData){
              this.vodLiveData = element
              console.log(this.vodLiveData)
            }
          })
        }
      }).catch(err => {
        console.log(err)
      })

      this.axios('/api/course', {
        params: {
          courseId: this.$route.params.id
        }
      }).then(data => {
        this.objectives = data
      }).catch(err => {
        console.log(err)
      })

    },
    methods: {
      intergal(){
        this.showIntergal = 1
      },
      closeIntergalModal(info){
        this.showIntergal = info
      }
    }
  }
</script>



